body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.App{text-align:center}.App-logo{height:40vmin;pointer-events:none}@media (prefers-reduced-motion: no-preference){.App-logo{-webkit-animation:App-logo-spin infinite 20s linear;animation:App-logo-spin infinite 20s linear}}.App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-link{color:#61dafb}@-webkit-keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}@keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}

.rff-converter{width:100%;padding:0 15%;box-sizing:border-box;text-align:center}.rff-form{display:flex;margin:0 auto;align-items:center;justify-content:center;flex-wrap:wrap;width:100%}.rff-orm,.rff-weeks{padding:5px;margin:5px 10px;border-radius:5px;border:1px solid rgba(119,119,119,0.5) !important}.rff-submitbtn{background-color:#0baa3b !important;color:#000 !important;border-color:#0baa3b !important}.headerimage{position:absolute;width:32px;height:32px;background-repeat:no-repeat;top:14px}.was-validated .form-control:valid,.form-control.is-valid{border:1px solid #ced4da !important;background-image:none !important}.rff-predictionview{position:absolute}.rff-chartview{position:absolute}.rff-chartnodata{position:relative;top:50%;right:15%;color:#777;z-index:999}.rff-chartcontainersidebyside{position:absolute;max-width:1140px;right:7%}.rff-chartcontainersingle{position:absolute;max-width:1140px;padding-left:10% !important}

.rff-chartheader{width:50%}

.loader{position:fixed;top:40%;left:50%;color:#0baa3b;z-index:999}

.alertcontainer{position:relative;z-index:99}.toastcontainer{position:absolute;top:0;right:0}.toast-body{color:red}.toast-header{color:red !important}

